import React from 'react';
import "Assets/Stylesheets/Views/AnimatedBackground.css";
import {symbols} from "Data/PictureData";
import Particles from "react-particles-js";

interface IAnimatedBackgroundProps
{

}

interface IAnimatedBackgroundState
{

}

class AnimatedBackground extends React.Component<IAnimatedBackgroundProps, IAnimatedBackgroundState>
{
    constructor(props:IAnimatedBackgroundProps)
    {
        super(props)

        this.GenerateList = this.GenerateList.bind(this);
    }

    render()
    {
        return(
            <Particles 
                params={{
                    "particles": {
                        "number": {
                            "value": 7,
                            "density": {
                                "enable": true,
                                "value_area": 800
                            }
                        },
                        "line_linked": {
                            "enable": false
                        },
                        "move": {
                            "speed": 5,
                            "out_mode": "bounce",
                            "bounce": true,
                            "random": true,
                        },
                        "shape": {
                            "type": [
                                "images"
                            ],
                            "images": this.GenerateList()
                        },
                        "opacity": {
                            random: true,
                        },
                        "size": {
                            value: 100,
                            random: true
                        }
                    }
                }}
                height="500"
                canvasClassName="animation-area"
            />
        )
    }

    private GenerateList(): any
    {
        let icons:any = []
        
        symbols.forEach(symb => {
            icons.push(
                {
                    "src": symb,
                    "height": 100,
                    "width": 100
                }
            )
        })
        return icons
    }
}
export default AnimatedBackground;