import React from "react";
import ProjectCard from "Components/ProjectCard";
import {projects} from "../Data/ProjectData";
import "../Assets/Stylesheets/Views/Projects.css";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import NavButton from "Components/NavButton";

interface IProjectGalleryProps
{

}

interface IProjectGalleryState
{

}

class Projects extends React.Component<IProjectGalleryProps, IProjectGalleryState>
{
    render()
    {
        return(
            <Container className="proj-container">
                <Row>
                    <Col xs sm md={12} lg xl>
                        <a id="projects" href="#projects">What I've done</a>
                    </Col>
                </Row>
                <Row style={{paddingBottom:"3%"}}>
                    <Col xs sm md={12} lg xl>
                        <h2 className="directions">Click on a project to see the source code</h2>
                    </Col>
                </Row>
                <div>
                    {
                        this.GenerateProjectRows()
                    }
                </div>
                <Row style={{paddingTop:"3vh"}}>
                    <Col xs sm md={12} lg style={{textAlign: "center"}}>
                        <NavButton label="Can I call you?" href="#contacts"/>
                    </Col>
                </Row>
            </Container>
        );
    }

    private GenerateProjectRows(): any
    {
        var numCols = 12 / projects.length;
        var numRows = Math.ceil(projects.length/numCols);
        
        
        var projectRows:any = [];
        for(let i=0; i<numRows; i++)
        {
            var startProjIndex = i*numCols;
            // add col to row and row to projects
            projectRows.push(
                <Row key={i}>
                    {
                        this.GenerateProjectColumns(numCols, startProjIndex)
                    }
                </Row>
            )
        }
        return projectRows;
    }

    private GenerateProjectColumns(numCols:number, startProjIndex:number): any
    {
        var cols:any = [];
        for(let i=0, j=startProjIndex; i<numCols && j<projects.length; i++, j++)
        {
            cols.push(
                <Col key={projects[j].projectName}>
                    <ProjectCard 
                        imgSrc={projects[j].imgSrc}
                        projectName={projects[j].projectName}
                        gitHubLink={projects[j].gitHub}
                        demoPics={projects[j].demoPics}
                        description={projects[j].description}
                    />
                </Col>
            )
        }
        return cols;
    }
}

export default Projects;