import React from "react";
import NameCard from "../Components/NameCard";
import NavButton from "Components/NavButton";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import "../Assets/Stylesheets/Views/Home.css"
import Tagline from "Components/Tagline";

interface IHomeProps
{

}

interface IHomeState
{

}

class HomeView extends React.Component<IHomeProps, IHomeState>
{
    componentDidMount () {
        this.scrollToHashId()
      }
    
      componentDidUpdate () {
        this.scrollToHashId()
      }
    
     
    render()
    {
        return(
            <Container className="home-background" id="home">
                <Row style={{paddingTop:"17vh", paddingBottom:"2vh"}}>
                    <Col xs sm md={12} lg xl={12}>
                        <NameCard />
                    </Col>
                </Row>
                <Row style={{paddingTop:"2vh", paddingBottom:"2vh"}}>
                    <Col xs sm md={12} lg xl={12} >
                        <Tagline/>
                    </Col>
                </Row>
                <Row style={{paddingTop:"2vh"}}>
                    <Col xs sm md={12} lg xl={12}  style={{textAlign: "center"}}>
                        <NavButton label="Who am I?" href="#about"/>
                    </Col>
                </Row>
            </Container>
        )
    }
    
    // emulate URL anchor page scroll functionality
    scrollToHashId () {
        // get URL hash (minus the hash mark)
        const hash = window.location.hash.substring(1)

        // if there's a hash, scroll to that ID
        if (hash && hash.length) {
            // setTimeout and requestAnimationFrame help ensure a true DOM repaint/reflow before we try to scroll
            setTimeout( () => {
                window.requestAnimationFrame(() => {
                    const el = document.getElementById(hash)
                    el.scrollIntoView()
                    // clean up the hash, so we don't scroll on every prop update
                    this.removeHash()
                })
            }, 0)
        }
    }

    removeHash () {
        const loc = window.location
        const hist = window.history
    
        // use modern browser history API
        if (hist && 'pushState' in hist) {
          hist.replaceState('', document.title, loc.pathname + loc.search)
        // fallback for older browsers
        } else {
          // prevent scrolling by storing the page's current scroll offset
          const scrollV = document.body.scrollTop
          const scrollH = document.body.scrollLeft
    
          loc.hash = ''
    
          // restore the scroll offset, should be flicker free
          document.body.scrollTop = scrollV
          document.body.scrollLeft = scrollH
        }
    }
}
export default HomeView;