import React from "react";
import "../Assets/Stylesheets/Components/PictureCard.css";
import Carousel from "react-bootstrap/Carousel";
import { mePics } from "../Data/PictureData";

interface IPictureCardState
{
    // slideshow pic
    imgSrc: string
}

interface IPictureCardProps
{
}

class PictureCarousel extends React.Component<IPictureCardProps, IPictureCardState>
{
    private readonly TIME_DELAY_SEC: number;
    private picIndex: number;

    constructor (props: IPictureCardProps)
    {
        super(props)
        this.picIndex = 0;
        this.TIME_DELAY_SEC = 10000; // 10sec

        this.generateCarouselItems = this.generateCarouselItems.bind(this);
    }

    render()
    {
        return(
            <Carousel controls={false} indicators={false} fade={true} touch={false}>
                {this.generateCarouselItems()}
            </Carousel>
        )
    }

    generateCarouselItems(): any
    {
        const carouselItems: any = [];
        mePics.forEach(picture => {
            carouselItems.push(
                <Carousel.Item key={picture}>
                    <img 
                        className="d-block mx-auto border border-white rounded"
                        src={picture}
                        id="picture"
                        alt="Me. Hello?"
                    />
                </Carousel.Item>
            )
        });
        return carouselItems;
    }
}
export default PictureCarousel;