import React from "react";
import "../Assets/Stylesheets/Components/ProjectPictureModal.css";
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import Carousel from "react-bootstrap/Carousel";

interface IProjectPictureModalState{
    
}

interface IProjectPictureModalProps{
    show:boolean
    toggleModal:any
    projectName: string
    demoPics: any
    description: string
}

class ProjectPictureModal extends React.Component<IProjectPictureModalProps, IProjectPictureModalState>
{
    private ToggleModal:any;

    constructor(props:IProjectPictureModalProps)
    {
        super(props);
        this.ToggleModal = this.props.toggleModal;
        this.ToggleModal = this.ToggleModal.bind(this);
    }

    render(){
        return (
            <Modal show={this.props.show} onHide={this.ToggleModal} animation={false} scrollable={true} size="xl">
                <Modal.Header>
                    <Modal.Title className="modal-text">{this.props.projectName}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <Carousel interval={null} nextIcon={<span aria-hidden="true" className="carousel-control-next-icon carousel-accent"/>}>
                        {this.generateCarouselItems()}
                    </Carousel>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="dark" className="modal-text btn-background" onClick={this.ToggleModal}>
                        Close
                    </Button>
                </Modal.Footer>
            </Modal>
        )
    }

    generateCarouselItems(): any
    {
        const carouselItems: any = [];
        this.props.demoPics.forEach((obj:any) => {
            carouselItems.push(
                <Carousel.Item key={obj}>
                    {/* eslint-disable-next-line jsx-a11y/img-redundant-alt*/}
                    <img src={obj.src} alt="Unable to load image" className="carousel-pic"/>
                    <Carousel.Caption className="modal-text carousel-accent">
                        {obj.description}
                    </Carousel.Caption>
                </Carousel.Item>
            )
        });
        return carouselItems;
    }
}
export default ProjectPictureModal;