import React from "react";
import "../Assets/Stylesheets/Components/NavButton.css"

interface IExploreProps
{
    label:string
    href:string
}

interface IExploreState
{

}

class NavButton extends React.Component<IExploreProps, IExploreState>
{
    render()
    {
        return(
            <a className="explore-btn" href={this.props.href}>
                {this.props.label}
            </a>
        )
    }
}
export default NavButton;