import React from 'react';
import Topbar from "./Shell/Topbar";
import HomeView from "./Views/Home";
import './App.css';
import AboutView from 'Views/About';
import BottomBar from "./Shell/BottomBar";
import ProjectsGallery from "Views/Projects";
import ContactsView from 'Views/Contacts';
import AnimatedBackground from "Views/AnimatedBackground";
import { BrowserRouter, Route } from 'react-router-dom';

class Container extends React.Component{
  render()
  {
      return (
      <div>
          <HomeView/>
          <AboutView/>
          <ProjectsGallery/>
          <ContactsView/>
      </div>
      );
  }
};

function App(){
  return(
    <BrowserRouter>
    <div data-spy="scroll" data-target=".navbar">
        <Topbar/>
            <Route path='/#home' component={Container}
                    onEnter={function(){
                        document.getElementById("home").scrollIntoView();
                    }
                }
            />
            <AnimatedBackground/>
            <Route path='/#about' component={Container}
                    onEnter={function(){
                        document.getElementById("about").scrollIntoView();
                    }
                }
            />
            <Route path='/#projects' component={Container}
            onEnter={function(){
                        document.getElementById("projects").scrollIntoView();
                    }
                }
            />
            <Route path='/#contacts' component={Container} 
                    onEnter={function(){
                        document.getElementById("contacts").scrollIntoView();
                    }
                }
            />
            <Route path='/' component={Container} />
        <BottomBar/>
    </div>
    </BrowserRouter>
  )
}
export default App;
